		filter: alpha(opacity = 30);
	}

	.note-control-handle,
	.note-control-sizing,
	.note-control-holder {
		width: 7px;
		height: 7px;
		border: 1px solid $black;
	}

	.note-control-sizing {
		background-color: $black;
	}

	.note-control-nw {
		top: -5px;
		left: -5px;
		border-right: none;
		border-bottom: none;
	}

	.note-control-ne {
		top: -5px;
		right: -5px;
		border-bottom: none;
		border-left: none;
	}

	.note-control-sw {
		bottom: -5px;
		left: -5px;
		border-top: none;
		border-right: none;
	}

	.note-control-se {
		right: -5px;
		bottom: -5px;
		cursor: se-resize;

		&.note-control-holder {
			cursor: default;
			border-top: none;
			border-left: none;
		}
	}

	.note-control-selection-info {
		right: 0;
		bottom: 0;
		padding: 5px;
		margin: 5px;
		color: $white;
		background-color: $black;
		font-size: 12px;
		border-radius: 5px;
		-webkit-opacity: 0.7;
		-khtml-opacity: 0.7;
		-moz-opacity: 0.7;
		opacity: 0.7;
		-ms-filter:  "progid: DXImageTransform.Microsoft.Alpha(Opacity=70)";
		filter: alpha(opacity = 70);
	}
}

.note-hint-popover {
	min-width: 100px;
	padding: 2px;

	.popover-content {
		padding: 3px;
		max-height: 150px;
		overflow: auto;

		.note-hint-group .note-hint-item {
			display: block !important;
			padding: 3px;

			&.active,
			&:hover {
				display: block;
				clear: both;
				font-weight: 400;
				line-height: 1.4;
				color: $white;
				white-space: nowrap;
				text-decoration: none;
				background-color: #428bca;
				outline: 0;
				cursor: pointer;
			}
		}
	}
}

.note-toolbar {
	background: #8080801d;
}

.note-btn-group .note-btn {
	border-color: #d7dcec;
	padding: 0.28rem 0.65rem;
	font-size: 13px;
}

.btn-group > .btn:not(:last-child):not(.dropdown-toggle) {
	border-top-right-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}
